@import '_variables.scss';

body {
    // 知乎
    font-family: -apple-system, BlinkMacSystemFont, Helvetica Neue, PingFang SC,
        Microsoft YaHei, Source Han Sans SC, Noto Sans CJK SC,
        WenQuanYi Micro Hei, sans-serif;
}

.hoverable.card-hover:hover {
    transform: scale(1.05);
    z-index: 100;
    width: 104%;
    transition: transform 0.3s ease-out;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.2), 0 5px 8px 0 rgba(0, 0, 0, 0.19);
    .card-content {
        background: $primary-color;
        height: 70px;
        padding: 10px;
        .grey-333,
        .grey-666,
        .grey-999 {
            color: white;
        }
    }
}

.card-space {
    padding: 0 5px !important;
}

// header
.tum-header {
    position: fixed;
    z-index: 1000;
    height: $header;
    display: flex;
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.4);
    .tum-navbar {
        display: flex;
        .tum-left {
            display: flex;
            width: 300px;
        }
        .tum-right {
            display: flex;
            width: 300px;
            .search-item {
                position: relative;
                input {
                    box-sizing: border-box;
                    font-size: 14px;
                    padding-left: 30px;
                    &:focus {
                        border-bottom: 1px solid $primary-color;
                        box-shadow: 0 1px 0 0 $primary-color;
                    }
                }
                .search-icon {
                    position: absolute;
                    top: 6px;
                    color: #333;
                }
            }
        }
        .tum-center {
            display: flex;
            flex: 1;
            justify-content: center;
        }
        .tum-item {
            width: 150px;
            padding: 12px 0;
            height: $header;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            p {
                line-height: 22px;
                margin: 0;
                font-size: 14px;
            }
        }
    }
    .tum-sign-button {
        padding: 0 20px;
        margin-left: 12px;
        font-size: 14px;
        color: white;
        background: $primary-color;
    }
    .tum-logo {
        background: url('../image/logo.png') center center no-repeat;
    }
    .nav-items {
        li {
            a {
                color: #000;
                height: $header;
                line-height: $header;
            }
        }
    }
}

// footer
.page-footer {
    background: $primary-color;
    padding-top: 8px;
    .tum-footer {
        background: $footer_bg;
        .footer-wrapper {
            display: flex;
            .footer-item {
                flex: 1;
                display: flex;
                flex-direction: column;
                .ft-logo {
                    margin: 54px 0 30px;
                }
                .ft-ps {
                    letter-spacing: 4px;
                    margin: 7px 12px;
                }
                .ft-top {
                    margin: 40px 0;
                    .ft-title {
                        line-height: 30px;
                        font-size: 18px;
                        margin: 18px 0 30px;
                    }
                    li {
                        font-size: 14px;
                        line-height: 30px;
                        margin-bottom: 8px;
                    }
                }
                .ft-bottom > img {
                    width: 140px;
                }
            }
        }
        .footer-copyright {
            font-size: 14px;
            background: $copyright_bg;
        }
    }
}

// background
.bg-grey {
    background: $gray_deep_bg;
}
.bg-grey-light {
    background: $gray_shadow_bg;
}

.hove_undeline:hover {
    text-decoration: underline;
}

// card fixed
.card {
    box-shadow: none;
    margin: 0;
    background: none;
}

.grey-999 {
    color: #999;
}
.grey-666 {
    color: #666;
}
.grey-333 {
    color: #333;
}



